body {
	background: linear-gradient(270deg,lightgreen,lightyellow,pink, #FFFF99, #CCFFFF,#FAFAD2, #FFEFD5);
	background-size: 500% 500%;
	animation: gradient 15s infinite;
}

.bg-img{
	background: linear-gradient(270deg,lightgreen,lightyellow,pink, #FFFF99, #CCFFFF,#FAFAD2, #FFEFD5);
	background-size: 500% 500%;
	animation: gradient 15s infinite;
}

.bot{
	background: url(https://alltop9.com/wp-content/uploads/2019/09/i6.png);
    background-repeat: no-repeat;
    background-size: 90%;
}

.help{
	background: url(https://www.smartbots.ai/wp-content/uploads/2019/04/bot-new.gif);
    background-repeat: no-repeat;
	background-size: 90%;
	position: fixed;
	bottom:-100px;
	right:-40px;
	height: 200px!important	;
	width: 200px;
	/* animation: blinker 1s linear infinite; */
}

.editBot{
	background: url(https://i.gifer.com/7BZk.gif);
    background-repeat: no-repeat;
	background-size: 90%;
	height: 50px;
	/* animation: blinker 1s linear infinite; */
}


@keyframes blinker {
	50% {
	  opacity: 0;
	}
  }

.right{
	position: absolute;
	right: 5%;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.logo{
	font-family: 'Lily Script One', cursive;
}